$columns: 12;
// $colMargin/2 = container padding
$colMargin: 30px;

$screen_hg: 1200px;
$screen_lg: 992px;
$screen_md: 768px;
$screen_sm: 576px;
$screen_xs: 420px;
$screen_xxs: 0px;

