$container_md: 960px;
$container_sm: 720px;
$container_xs: 540px;
$container_xxs: 400px;
$container_xxxs: 360px;





@mixin grid-wrapper{

    @include media-hg{max-width: $container_hg;}
    @include media-lg{max-width: $container_lg;}
    @include media-md{max-width: $container_md;}
    @include media-sm{max-width: $container_sm;}
    @include media-xs{max-width: $container_xs;}
    @include media-xxs{max-width: $container_xxs;}

    margin: 0px auto;
    //compensate row -margin
    padding: 0 grid_row_margin();
    height: 100%;
}


@mixin grid-row-flex{
    margin-left: -#{$colMargin/2};
    margin-right: -#{$colMargin/2};
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@function grid_row_margin(){
    @return $colMargin/2;
}

%col{
    // box-sizing:border-box;
    // margin: 0 $colMargin/2;
    padding: 0 $colMargin/2;
    
    // &:first-child{
        //     margin-left: 0px;
        // }
    // &:last-child{
    //     margin-right: 0px;
    // }
}

@mixin col($col){
    // margin-left: $colMargin/2;
    // margin-right: $colMargin/2;
    padding-left: $colMargin/2;
    padding-right: $colMargin/2;

    $procWidth: 100*$col/$columns;
    width: calc(#{$procWidth}% - #{$colMargin});

}

//calc not working in operamini ie8
@mixin col-hg($col){
    // @extend %col;
    // @include col($col);
    @include media-hg{
        @include col($col);
    }
}

