@mixin placeholder {
    ::-webkit-input-placeholder {@content;}
    :-moz-placeholder           {@content;}
    ::-moz-placeholder          {@content;}
    :-ms-input-placeholder      {@content;}
  }


@mixin clearfix{
  //clearfix
  &::after{
      content:'';
      display: block;
      clear: both;
  }
}

// px to rem
@function rem($pixels, $context: $mainFontSize) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  @if (unitless($context)) {
    $context: $context * 1px;
  }
  @return $pixels / $context * 1rem;
}