$color_title: #5572d6;
$color_text: #bab9ba;
$mainFontBold: RobotoBold, Helvetica, Arial, sans-serif;
$mainFontRegular: RobotoRegular, Helvetica, Arial, sans-serif;
$mainFontMedium: RobotoMedium, Helvetica, Arial, sans-serif;
$mainFontLight: RobotoLight, Helvetica, Arial, sans-serif;
$mainFontThin: RobotoThin, Helvetica, Arial, sans-serif;

.main-section {
  background-color: #85a5d4;
  padding-top: 100px;
  
  @include media-md {
    height: 100vh;
  }
  @include media-md {
    padding-bottom: 35vh;
  }
  @include media-xs {
    padding-bottom: 35vh;
  }
  @include media-xxs {
    padding-bottom: 100px;
    height: auto;
  }

  //   .container {
  //     @include clearfix;
  //     @include grid-wrapper;
  //   }
  .container {
    @include clearfix;
    @include media-xxs {
      padding-left: 40px;
      padding-right: 40px;
    }
    @include media-xxs {
      //   padding-left: 10px;
      //   padding-right: 10px;
    }

    .card-block {
      @include media-xxs {
        position: relative;
        height: auto;
        min-width: 250px;
      }
      @include media-md {
        height: 335px;
      }

      box-sizing: border-box;
      max-width: 900px;
      margin: 0 auto;
      padding: 15px;
      background: #ffffff;
      border-radius: 4px;
      box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

      display: flex;
      flex-direction: column;
      justify-content: space-around;

      .cards {
        display: flex;
        justify-content: space-between;

        @include grid-row-flex;
        padding-left: 15px;
        padding-right: 15px;

        .card {
          @include col-lg(3);
          @include col-md(4);
          @include col-sm(6);
          @include col-xs(12);
          @include col-xxs(12);
          //   height: 290px;

          flex-grow: 1;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: center;
          padding-top: 15px;
          height:230px;

          &:hover {
            background-color: #f4f5f2;
            cursor: pointer;
          }

          &__img {
            width: 60px;
          }
          &__title {
            font-family: $mainFontBold;
            letter-spacing: 4px;
            color: $color_title;
            font-size: rem(16);
            // text-transform: capitalize;
            text-transform: uppercase;
            margin-top: 15px;
            margin-bottom: 5px;
          }

          &__text {
            width: 220px;
            color: $color_text;
            text-align: center;
            font-size: rem(12);
            // align-self: flex-start;
            min-height: 65px;
          }
          &__price {
            position: relative;
            display: block;
            width: 40px;
            color: $color_title;
            font-family: $mainFontBold;
            font-size: rem(25);

            &-sign {
              position: absolute;
              top: 7px;
              left: -7px;
              font-size: rem(12);
            }
            &-bottom {
              display: block;
              // height:10px;
              width: 40px;
              position: absolute;
              bottom: 7px;
              left: 32px;
              font-size: rem(11);
              font-family: $mainFontMedium;
            }
          }
        }
      } //cards

      .button {
        margin: 20px auto;
      }
    } //card-block
  }
} //section-1

.button {
  display: block;
  width: 100px;
  padding: 8px 16px;
  color: #fff;
  border-radius: 4px;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.08);
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: background-color 0.1s linear;

  cursor: pointer;
  width: 160px;
}
.btn--feedback {
  $bkgColor: #2d54ff;
  $borderColor: darken($bkgColor, 20%);
  background-color: $bkgColor;
  border: $borderColor;

  // background-color: rgb(43, 153, 91);
  // border: 1px solid rgb(33, 126, 74);
}

.modal {
  @include media-xxs {
    width: 330px;
  }
  @include media-xs {
    width: 400px;
  }
  display: none;
  height: 335px;
  position: relative;
  background-color: white;
  border-radius: 4px;

  &__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    background-image: url(../img/cross.svg);
    background-repeat: no-repeat;
    z-index: 9012;
    cursor: pointer;
  }

  &__title {
    height: 20px;
    margin-top: 20px;
    color: #000000;
    text-align: center;
    font-size: rem(14);
  }

  .card {
    height: 250px;
    padding-top: 20px;

    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__img {
      width: 60px;
    }
    &__title {
      font-family: $mainFontBold;
      letter-spacing: 4px;
      color: $color_title;
      font-size: rem(16);
      // text-transform: capitalize;
      text-transform: uppercase;
      //   margin-top: 25px;
      //   margin-bottom: 15px;
    }

    &__text {
      width: 300px;
      color: $color_text;
      text-align: center;
      font-size: rem(12);
      //   min-height: 65px;
    }
    &__price {
      position: relative;
      display: block;
      width: 40px;
      color: $color_title;
      font-family: $mainFontBold;
      font-size: rem(25);

      &-sign {
        position: absolute;
        top: 7px;
        left: -7px;
        font-size: rem(12);
      }
      &-bottom {
        display: block;
        // height:10px;
        width: 40px;
        position: absolute;
        bottom: 7px;
        left: 32px;
        font-size: rem(11);
        font-family: $mainFontMedium;
      }
    }
  }

  .button {
    margin: 0 auto;
    margin-top: 0;
    margin-bottom: 20px;
  }

  // position: fixed;
  // z-index: 9001;
  // left: 50%;
  // top: 50%;
  // margin-left: -557.5px;
  // margin-top: -57.5px;
}
